:root {
    --darkest-bg-color: #1e2124;
    --second-darkest-bg-color: #282b30;
    --standard-bg-color: #36393e;
    --light-standard-alternative: #424549;
    --light-purple: #7289da;
    --light-purple-hover: #627ddf;
    --white: #e4e5e6;
    --light-grey: #a0a1a1;
}

.fs-30 {
    font-size: 30px;
}

body {
    background-color: var(--standard-bg-color);
    color: var(--white);
}

main {
    flex: 1;
}

/* ---- header styling ---- */
.header-div {
    justify-content: space-between;
}

h1 {
    line-height: 70px;
}

.header-ul {
    gap: 30px;
    list-style: none;
    line-height: 70px;
}

.first-button, .second-button {
    color: rgb(255, 255, 255, 75%);
    transition: 0.3s;
}

.first-button:hover, .second-button:hover {
    color: rgb(255, 255, 255, 100%);
    transition: 0.3s;
}

.main-button {
    background-color: var(--light-purple);
    padding: 10px 20px;
    border-radius: 50px;
    transition: 0.3s;
}

.main-button:hover {
    background-color: var(--light-purple-hover);
    transition: 0.3s;
}

header {
    height: 70px;
    background-color: var(--darkest-bg-color);
    
}

/* ---- banner styling ---- */

.banner {
    background-color: var(--light-standard-alternative);
}

.banner-div {
    background-color: var(--light-standard-alternative);
    padding: 70px 0;
    flex-direction: column;
    align-items: center;
    h2 {
        font-size: 50px;
    }
    p {
        font-size: 25px;
    }

}

.banner-buttons-div {
    margin-top: 30px;
    flex-direction: row;
    gap: 30px;
}

.banner-button-1 {
    background-color: rgb(255, 255, 255, 0%);
    color: var(--white);
    border-radius: 50px;
    border: 1px solid rgb(255, 255, 255, 50%);
    transition: 0.3s;

}

.banner-button-1:hover{ 

        border: 1px solid rgb(255, 255, 255, 100%);
        transition: 0.3s;
}

.banner-button-2 {
    background-color: var(--light-purple);
    color: var(--white);
    border-radius: 50px;
    transition: 0.3s;
}

.banner-button-2:hover {
    transition: 0.3s;
    background-color: var(--light-purple-hover);
}

button {
    padding: 10px 20px;
}

/* ---- gallery styling ---- */
.gallery-div {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 100px;
    gap: 20px;
}

.gallery-img {
    height: 200px;
    width: 250px;
    object-fit: cover;
    border-radius: 10px;
}


/* ---- footer styling ---- */

footer {
    background-color: var(--second-darkest-bg-color);
    height: 90px;
    width: 100%;
    position: absolute;
    bottom: 0;
}

.footer-div {
    justify-content: space-between;
    line-height: 90px;
    color: var(--light-grey);
}

.icons {
    display: flex;
    align-items: center;
    line-height: 90px;
    gap: 20px;
}
